import React, { createContext, FC, memo, ReactNode } from 'react';

export const AnalyticsSubGroupIdContext = createContext<string | null>(null);

export interface AnalyticsSubGroupProviderProps {
    id: string;
    children: ReactNode;
}

const AnalyticsSubGroupProvider: FC<AnalyticsSubGroupProviderProps> = memo(
    ({ id, children }) => {
        return (
            <AnalyticsSubGroupIdContext.Provider value={id}>
                {children}
            </AnalyticsSubGroupIdContext.Provider>
        );
    }
);

AnalyticsSubGroupProvider.displayName = 'AnalyticsSubGroupProvider';

export default AnalyticsSubGroupProvider;
